import { Link } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import "bootstrap/dist/css/bootstrap.min.css";
import { Swiper, SwiperSlide } from "swiper/react";
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import { Autoplay, EffectFlip, Pagination, Parallax, Navigation } from "swiper";

const Home = () => {
  const { t, i18n } = useTranslation();
  const langUrl = i18n.language === '' ? '/en' : '/' + i18n.language;

  return (
    <>
      <div className="home-swiper-container">
        <div className="bg-dark pb-4">
          <Swiper
            spaceBetween={30}
            slidesPerView={1.1}
            centeredSlides={true}
            autoplay={{
              delay: 2500,
              disableOnInteraction: false,
            }}
            pagination={{
              el: '.home-swiper-pagination',
              clickable: true,
              renderBullet: (index, className) => {
              return '<span class="' + className + '"></span>';
              },
            }}
            parallax={true}
            loop={true}
            navigation={true}
            effect={"flip"}
            modules={[Autoplay, Pagination, Parallax, Navigation]}
            className="home-swiper"
          >
            <SwiperSlide>
              <img src={process.env.PUBLIC_URL + '/images/web-banner/1.webp'} className="card-img-top" alt="" />
            </SwiperSlide>
            <SwiperSlide>
              <img src={process.env.PUBLIC_URL + '/images/web-banner/2.webp'} className="card-img-top" alt="" />
            </SwiperSlide>
            <SwiperSlide>
              <img src={process.env.PUBLIC_URL + '/images/web-banner/3.webp'} className="card-img-top" alt="" />
            </SwiperSlide>
            <SwiperSlide>
              <img src={process.env.PUBLIC_URL + '/images/web-banner/4.webp'} className="card-img-top" alt="" />
            </SwiperSlide>
          </Swiper>
        </div>
        <div className="home-swiper-pagination" />
      </div>

      <div className="container">
        <div className="mt-3 list row">
          <div className="col-md-12">
            <h4 className="mt-3">{t('home.title')}</h4>
          </div>
        </div>
        <div className="mt-3 list row">
          <div className="col-md-4 p-2">
            <div className="card">
              <img src={process.env.PUBLIC_URL + '/images/web-banner/1.webp'} className="card-img-top" alt="" />
              <div className="card-body">
                <h5 className="card-title">MetaMask</h5>
                <p className="card-text">Testing MetaMask with ethers.js</p>
                <div className="text-center">
                  <Link to={langUrl + "/metamask"} className="btn btn-primary">
                  View
                  </Link>
                </div>
              </div>
            </div>
          </div>
          
          <div className="col-md-4 p-2">
            <div className="card">
              <img src={process.env.PUBLIC_URL + '/images/web-banner/2.webp'} className="card-img-top" alt="" />
              <div className="card-body">
                <h5 className="card-title">WalletConnect</h5>
                <p className="card-text">Testing WalletConnect with ethers.js</p>
                <div className="text-center">
                  <Link to={langUrl + "/metamask"} className="btn btn-primary">
                  View
                  </Link>
                </div>
              </div>
            </div>
          </div>

          <div className="col-md-4 p-2">
            <div className="card">
              <img src={process.env.PUBLIC_URL + '/images/web-banner/3.webp'} className="card-img-top" alt="" />
              <div className="card-body">
                <h5 className="card-title">Card title</h5>
                <p className="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</p>
                <div className="text-center">
                  <Link to={langUrl + "/metamask"} className="btn btn-primary">
                  View
                  </Link>
                </div>
              </div>
            </div>
          </div>

          <div className="col-md-4 p-2">
            <div className="card">
              <img src={process.env.PUBLIC_URL + '/images/web-banner/3.webp'} className="card-img-top" alt="" />
              <div className="card-body">
                <h5 className="card-title">Card</h5>
                <p className="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</p>
                <a href="#" className="btn btn-primary">Goe</a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  )
}

export default Home